<template>
  <div id="multi" tabindex="-1">
    <p v-if="!levelReady">WAITING FOR OTHER PLAYER TO LOAD</p>
    <svg :style="style" @mousemove="ejaculate" v-if="levelReady && !winner">
      <ovum
        v-for="ovum in ovums"
        :key="ovum.id"
        :id="ovum.id"
        :d="ovum.shape"
        :width="width"
        :height="height"
        :originX="place(ovum.x)"
        :originY="place(ovum.y, true)"
        :originVX="ovum.vx"
        :originVY="ovum.vy"
        :vr="ovum.vr"
        :frame="frame"
      />
      <sperma
        v-for="(player, index) in players"
        :key="player.id"
        :id="player.id"
        :color="player.color"
        :width="width"
        :height="height"
        :target="target"
        :origin-x="width/4 + (index%2) * width/2"
        :origin-y="height/4 + Math.floor(index/2) * height/2"
        :frame="frame"
        :viscosity="viscosity"
        :room="room"
        :current-player="currentPlayer"
      />
      <multi-score
        v-for="(player, index) in players"
        :key="player.scoreId"
        :id="player.id"
        :room="room"
        :current-player="currentPlayer"
        :x="positionateScore(index)"
        :color="player.color"
      />
    </svg>
    <div v-if="levelReady && winner" class="winner">
      <div>The winner is : <h1 :style="{color: winnerColor}">{{winner.name}}</h1></div>
      <button style="margin: auto" @click="back">Back to Room</button>
    </div>
  </div>
</template>

<script>
import Sperma from "../sprites/Sperma";
import Ovum from "../sprites/Ovum";
import MultiScore from "../shapes/MultiScore"
import store from "../store";
export default {
  name: "multi",
  store,
  components: {
    Sperma,
    Ovum,
    MultiScore
  },
  props: {
    height: {
      type: Number,
      default: 570
    },
    width: {
      type: Number,
      default: 760
    },
    starters: {
      type: Array,
      default() {
        return [];
      }
    },
    currentPlayer: {
      type: Number,
      default: 0
    },
    room: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      target: { x: 300, y: 400 },
      frame: 0,
      viscosity: 0.002,
      sprites: [],
      ovums: [],
      scheme: ["#FA5C18", "#185CF9", "#F9B218", "#D81815"],
      assetsLoaded: false,
      levelReady: false,
      players: [],
      winner: null
    };
  },
  socket: {
    events: {
      loaded(data) {
        this.players[this.players.map(p => p.id).indexOf(data.id)].loaded = true
        if(this.players.every(player => player.loaded)) {
          this.levelReady = true
        }
      }
    }
  },
  mounted() {
    this.$store.dispatch("ResetWinner")
    this.players = this.starters.map((player, i) => {
      player.color = this.scheme[i];
      player.score = 0;
      player.scoreId = `sc-${player.id}`;
      player.loaded = false
      if (this.currentPlayer !== player.id)
        this.$store.dispatch("addOpponent", player);
      return player;
    });
    this.loadAssets();
    this.$store.dispatch("enteredRoom", this.room);
  },
  methods: {
    place(percent, isVertical) {
      return percent * (isVertical ? this.height : this.width);
    },
    positionateScore(index) {
      if (this.players.length)
        return (
          this.width * (0.5 / this.players.length + index / this.players.length)
        );
      return this.width / 2;
    },
    ejaculate(e) {
      this.target = { x: e.clientX, y: e.clientY };
    },
    animate() {
      this.frame = requestAnimationFrame(this.animate);
    },
    loadAssets() {
      fetch(`${process.env.VUE_APP_API_URL}/sprite/`, {
        mode: "cors",
        credentials: "include"
      })
        .then(response => {
          return response.json();
        })
        .then(sprites => {
          this.sprites = sprites;
          this.assetsLoaded = true;
        });
    },
    back () {
      this.$emit('back')
    }
  },
  watch: {
    '$store.state.collision' (collision) {
      if (collision) {
        this.$io.socket.post(`/multi/${this.room}/collision`, {
          id: collision.id,
          vx: collision.vx,
          vy: collision.vy,
          stunned: collision.stunned
        });
        this.$store.dispatch('resetCollision')
      }
    },
    '$store.state.winner' (id) {
      this.winner = this.players.find(player => player.id === id)
    },
    assetsLoaded(isReady) {
      if (isReady) {
        this.ovums = [
          {
            id: 0,
            name: "alpha",
            shape: this.sprites.find(sprite => sprite.name === "alpha").shape,
            type: "ovum",
            vr: 1,
            vx: 0,
            vy: 0,
            x: 0.5,
            y: 0.5
          }
        ];
        this.$io.socket.get(
          `/multi/${this.room}/${this.currentPlayer}`,
          (room, res) => {}
        );
      }
    },
    levelReady(isReady) {
      if(isReady) {
        this.animate();
      }
    }
  },
  computed: {
    style() {
      return { height: `${this.height}px`, width: `${this.width}px` };
    },
    winnerColor() {
      //if(this.winner) {
      return this.scheme[this.players.map(player => player.id).indexOf(this.winner.id)]
      //}
      //return "#ffffff"
    }
  },
  beforeDestroy() {
    cancelAnimationFrame(this.frame);
  }
};
</script>

<style>
#multi {
  width: 100%;
  height: 100%;
  background-color: black;
}
#multi:focus {
  border: none;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}
.winner {
  padding: 20% 0;
}
</style>
